import React, { useState } from 'react';
import HomePage from 'components/HomePage/HomePage';
import PostItem from './PostItem';
import { nanoid } from 'nanoid';

import styles from './posts.module.css';

const Posts = () => {
  const [posts, setPosts] = useState([]);  //        ,       
    // {
    //   id: nanoid(),
    //   name: 'dsfafsdfdsaf',
    //   description: 'dsfdsfffffffc dwaasdadsaa afd fddf afdfdf fdef',
    // },
    // {
    //   id: 2,
    //   name: 'Name 2',
    //   description: 'hto svjazana s podtom, to est kratkoe soderzhanie',
    // },
    // { id: 3, name: 'Name 3', description: 'A zdes budet vsjakaj vsjachena ch' },
  //]);

//   const [name, setName] = useState(''); //     ,     
//   const [description, setDescription] = useState('');

const [post, setPost] = useState({name: '', description: ''})//       input,        
  
const addNewPost = e => {
    e.preventDefault();
    //console.log(name, description); //   input
    // const newPost = {  //       
    //   id: nanoid(), 
    //   name,
    //   description,
    // };
    // console.log(newPost);
    // setPosts([...posts, newPost]);    //       
    //                                   //   ,      (...posts)           (setPost)
    // setName(''); //    input
    // setDescription('');
    setPosts([...posts, {...post, id:nanoid()}]);
    setPost({name:'', description:''});
    };

  const removePost = (id) => {                //  id ,   ,    posts  ,   .  id   p   id,   removePost,       .
                                              //  id   p  id,   removePost,        .
    setPosts(posts.filter(p => p.id !== id))
  }

  return (
    <div>
      <HomePage />
      <form className={styles.form} action="">
        <h1 className={styles.title}>Posts List</h1>
        <input
          className={styles.input}
          type="text"
          placeholder="Post's name"
          //value={name} //  value,  inputa    name
          value={post.name}
          //onChange={e => setName(e.target.value)} //  onChange,        
          onChange={e => setPost({...post, name: e.target.value})} //          onChange,         (...post),       (name) 
        />
        <input
          className={styles.input}
          type="text"
          placeholder="Description"
          //value={description}
          value={post.description}
          //onChange={e => setDescription(e.target.value)}
          onChange={e => setPost({...post, description: e.target.value})}
        />
        <button className={styles.btn} type="submit" onClick={addNewPost}>
          Create new post
        </button>
      </form>
      {posts.length > 0 ? (
        posts.map((post, index) => <PostItem post={post} number={index+1} key={post.id} remove={removePost}/>)
      ) : (
        <p>No posts available</p>
      )}
    </div>
  );
};

export default Posts;
