import HomePage from 'components/HomePage/HomePage';
import React, { useMemo, useState } from 'react';
import styles from './bookList.module.css';
import { nanoid } from 'nanoid';
import BookItem from './BookItem';
import MySelect from './MySelect';
import FindBook from './FindBook';
import MyModal from './MyModal';

const BookList = () => {
  const [books, setBooks] = useState([
    //    
    {
      id: nanoid(),
      name: 'Leon King',
      author: 'Konan Doil',
    },
    {
      id: nanoid(),
      name: 'Svejk',
      author: 'Capek',
    },
    { id: nanoid(), name: 'Kazka', author: 'Hans Christian Anderson' },
  ]);

  const [book, setBook] = useState({ name: '', author: '' }); //    
  const [selectedSort, setSelectedSort] = useState(''); //    
  const [searchQuery, setSearchQery] = useState(''); //     
  const [modal, setModal] = useState(false); //           

  //  useMemo:
  // : () => { ... }  ,    .
  //  : [selectedSort, books]   ,      .

  const sortedBook = useMemo(() => {
    // ,            ,    .
    // useMemo              selectedSort  books.
    console.log('working');
    if (selectedSort) {
      //  selectedSort  ,         (name  author)   localeCompare.
      return [...books].sort((a, b) =>
        a[selectedSort].localeCompare(b[selectedSort])
      ); // a     localeCompare    books      (name  author)
    }
    return books; //  selectedSort ,    books.
  }, [selectedSort, books]);

  const sortedAndSearchedBook = useMemo(() => {
    //       sortedBook, ,     (   )  searchQuery (    ).
    // useMemo              searchQuery  sortedBook.
    return sortedBook.filter(book =>
      book.name.toLowerCase().includes(searchQuery)
    );
  }, [searchQuery, sortedBook]);

  const addNewBook = (e) => {
    e.preventDefault();
    // console.log(book.name, book.author);
    //     const newBook = {
    //         id: nanoid(),
    //         name: book.name,
    //         author: book.author
    // };
    //     setBook([...book, newBook]);
    //     console.log(newBook)
    // 
    setBooks([...books, { ...book, id: nanoid() }]); //  ,   ,   book,    {book, id: nanoid()}.
    setBook({ name: '', author: '' });
    setModal(false);
  };

  const remove = id => {
    setBooks(books.filter(book => book.id !== id));
  };

  const sortBooks = sort => {
    //    
    setSelectedSort(sort);
    console.log(sort); // sort    name, author
    // setBooks([...books].sort((a, b) => a[sort].localeCompare(b[sort]))); //      // a     localeCompare    books      (name  author)
  };

  return (
    <>
      <HomePage />
      <button className={styles.btn} onClick={() => setModal(true)}>Create book list</button>
      <MyModal visible={modal} setVisible={setModal}>
        <form className={styles.form}>
        <h1 className={styles.title}>Book List</h1>
        <input
          className={styles.input}
          type="text"
          placeholder="Book's name"
          value={book.name}
          onChange={e => setBook({ ...book, name: e.target.value })}
        />
        <input
          className={styles.input}
          type="text"
          placeholder="Author"
          value={book.author}
          onChange={e => setBook({ ...book, author: e.target.value })}
        />
        <button className={styles.btn} type="submit" onClick={addNewBook}>
          Add new book
        </button>
      </form>
      </MyModal>
      
      <div style={{ display: 'flex', justifyContent: 'center' }}>
        <MySelect
          value={selectedSort}
          onChange={sortBooks}
          defaultValue="sort"
          options={[
            { value: 'name', name: 'by name' },
            { value: 'author', name: 'by author' },
          ]}
        />
      </div>
      <FindBook
        value={searchQuery}
        onChange={event => setSearchQery(event.target.value)}
      />
      {sortedAndSearchedBook.length > 0 ? (
        <>
          <ul className={styles.list}>
            {sortedAndSearchedBook.map(book => (
              <BookItem book={book} key={book.id} removeBook={remove} />
            ))}
          </ul>
        </>
      ) : (
        <p>The list is empty!</p>
      )}
    </>
  );
};

export default BookList;
